import React, { Component } from 'react';

class Header extends Component {
  render() {
    const t = this.props.translations?.nav || {};
    const lang = this.props.language || 'pl';

    if(this.props.data){
      var name = this.props.data.name;
      var occupation= this.props.data.occupation;
      
      // Handle bilingual description
      var description = typeof this.props.data.description === 'object' 
        ? this.props.data.description[lang] 
        : this.props.data.description;
        
      var city= this.props.data.address.city;
      var networks= this.props.data.social.map(function(network){
        return <li key={network.name}><a href={network.url}><i className={network.className}></i></a></li>
      })
    }

    return (
      <header id="home">

      <nav id="nav-wrap">

         <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
	      <a className="mobile-btn" href="#home" title="Hide navigation">Hide navigation</a>

         <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">{t.home || 'Do gry'}</a></li>
            <li><a className="smoothscroll" href="#about">{t.about || 'O mnie'}</a></li>
	         <li><a className="smoothscroll" href="#resume">{t.resume || 'yciorys'}</a></li>
            <li><a className="smoothscroll" href="#portfolio">{t.portfolio || 'Projekty'}</a></li>
         </ul>

      </nav>

      <div className="row banner">
         <div className="banner-text">
            <h1 className="responsive-headline"> {name}</h1>
            <h3>{description}</h3>
            <hr />
            <ul className="social">
               {networks}
            </ul>
         </div>
      </div>

      <p className="scrolldown">
         <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
      </p>

   </header>
    );
  }
}

export default Header;